@import 'variables.scss';

.button {
    padding: .7rem 2.3rem;
    margin: 2rem 0;
    background-color: $pri;
    outline: none;
    border: none;
    border-radius: 5rem;
    color: white;
    font-family: $f1;
    font-size: 1.6rem;
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    &:hover {
        background-color: darken(#0095E0, 10);
        transition: all .2s ease-in-out;
    }
}

.btn-center {
    display: inline-flex;
}